#homes{
    margin: auto;
    width: 1200px;
    height: 500px;
    background-color: midnightblue;
}
h2{
    font-size:60px;
    color: aqua;
    padding-top: 10%;
}
p{
    color: #fff;
    font-size: 35px;
    margin-top: 1%;
}
#button1{
    background-color: aqua;
    border: aqua solid 1px;
    color: darkslategrey;
    font-size: 30px;
    margin-top: 5%;
    width: 180px;
    height: 80px;
    font-size: 30px;
    cursor: pointer;
}
#button2{
    background-color: transparent;
    border: transparent solid 1px;
    color:aqua;
    margin-left: 5px;
    width: 180px;
    height: 80px;
    font-size: 30px;
    cursor: pointer;
}